import React from 'react';
import Home from './home';
import Contact from './contact';
import About from './about';
import Service from './services';
import Livestreaming from './livestreaming';
import Video from './video';
import Media from './media';
import Podcasting from './podcasting';

import {BrowserRouter as Router, Link, Switch, Route} from 'react-router-dom'

export default function Nav() {
    return (
        <>
        <Router>
            <header className="header-medea clearfix header-sticky">
                <div className="container-fluid ">
                    <div className="row no-gutters">
                        <div className="col-lg-12">
                            <div className="header-medea-inner-area">
                                <div className="left-side">
                                    <div className="logo-medea">
                                        <Link to="/"><img src="images/logo.png" alt="" /></Link>
                                    </div>
                                </div>
                                

                                <div className="right-side d-flex">
                                    
                                    {/* search-input-box start */}
                                    <div className="search-input-box">
                                        <input type="text" placeholder="Search" />
                                        <button><i className="zmdi zmdi-search"></i></button>
                                    </div>
                                    {/* <!-- search-input-box end --> */}

                                </div>
                            </div>
                            
                            <div className="col">
                                <div className="mobile-sidebar d-block d-lg-none">
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </header>

            <Switch>
                <main className="page-content-wrapper">
                <div className="side-main-menu">
                    <nav className="sidebar-menu" data-simplebar>
                        <ul>
                            <li className="normal-item-pro">
                                <Link to="livestreaming">
                                    <i className="zmdi zmdi-tv-alt-play"></i>
                                    <span>Livestreaming</span>
                                </Link>
                            </li>
                            <li className="normal-item-pro">
                                <Link to="podcasting">
                                    <i className="zmdi zmdi-collection-music"></i>
                                    <span>Podcasting</span>
                                </Link>
                            </li>
                            <li className="normal-item-pro">
                                <Link to="video">
                                    <i className="zmdi zmdi-cocktail"></i>
                                    <span>Video Production</span>
                                </Link>
                            </li>
                            <li className="normal-item-pro">
                                <Link to="media">
                                    <i className="zmdi zmdi-slideshare"></i>
                                    <span>Media Consultancy</span>
                                </Link>
                            </li>
                        </ul>
                    </nav>
                </div>

                               
                {/* <Sidenav /> */}
                <Route exact path="/">
                    <Home />
                </Route>
                <Route path="/livestreaming">
                    <Livestreaming />
                </Route>
                <Route path="/podcasting">
                    <Podcasting />
                </Route>
                <Route path="/about">
                    <About />
                </Route>
                <Route path=".service">
                    <Service />
                </Route>
                <Route path="/contact">
                    <Contact />
                </Route>
                <Route path="/video">
                    <Video />
                </Route>
                <Route path="/media">
                    <Media />
                </Route>


                <footer className="footer-area coloer-three">
                    <div className="footer-top-one footer-bg-image-two">
                        <div className="container-coustom">
                            <div className="row">   
                                <div className="col-custom-4 mt--50">
                                    {/* <!-- footer-widget --> */}
                                    <div className="footer-widget">
                                        <h4 className="footer-widget-title">About ViewMax</h4>

                                        <div className="footer-contet">
                                            <p>ViewMax is a digital livestreaming platform. We stream contents that educate, entertain and elevate you.</p>
                                            <ul className="fotter-socail">
                                                <li><Link to="#"><i className="zmdi zmdi-facebook"></i></Link></li>
                                                <li><Link to="#"><i className="zmdi zmdi-twitter"></i></Link></li>
                                                <li><Link to="#"><i className="zmdi zmdi-linkedin"></i></Link></li>
                                                <li><Link to="#"><i className="zmdi zmdi-instagram"></i></Link></li>
                                            </ul>
                                        </div>

                                    </div>
                                    {/* <!--// footer-widget --> */}
                                </div>

                                <div className="col-custom-3 mt--50">
                                    {/* <!-- footer-widget --> */}
                                    <div className="footer-widget">
                                        <h4 className="footer-widget-title">Company</h4>

                                        <div className="footer-contet">
                                            <ul className="footer-list">
                                                <li><Link to="/">Home</Link></li>
                                                <li><Link to="about">About</Link></li>
                                                <li><Link to="service">Service</Link></li>
                                                <li><Link to="contact">Contact</Link></li>
                                            </ul>
                                        </div>

                                    </div>
                                    {/* <!--// footer-widget --> */}
                                </div>

                                <div className="col-custom-3 mt--50">
                                    {/* <!-- footer-widget --> */}
                                    <div className="footer-widget">
                                        <h4 className="footer-widget-title">Services</h4>

                                        <div className="footer-contet">
                                            <ul className="footer-list">
                                                <li><Link to="livestreaming">Livestreaming
                                                    </Link></li>
                                                <li><Link to="podcasting">Podcasting
                                                    </Link></li>
                                                <li><Link to="video">Video Production
                                                </Link></li>
                                                <li><Link to="media"> Media Consultancy</Link></li>
                                            </ul>
                                        </div>

                                    </div>
                                    {/* <!--// footer-widget --> */}
                                </div>

                                <div className="col-custom-3 mt--50">
                                    {/* <!-- footer-widget --> */}
                                    <div className="footer-widget">
                                        <h4 className="footer-widget-title">Contact</h4>

                                        <div className="footer-contet">
                                            <ul className="footer-contact-list">
                                                <li> <i className="zmdi zmdi-phone"></i> <Link to="tel:08064806168">+2348064806168</Link></li>
                                                <li> <i className="zmdi zmdi-home"></i> <Link to="#">Abia Court, Gaduwa Estate, Gudu Abuja.</Link>
                                                </li>
                                                <li> <i className="zmdi zmdi-email"></i> <Link to="#">info@viewmax.tv</Link></li>
                                            </ul>
                                        </div>

                                    </div>
                                    {/* <!--// footer-widget --> */}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="footer-bottom">
                        <div className="container-coustom">
                            <div className="row">
                                <div className="col-lg-6 col-md-6">
                                    <p className="copyright-text">&copy; 2021 All right reserved. Powered By: Enadama Group</p>
                                </div>
                                <div className="col-lg-6 col-md-6">
                                    <ul className="footer-bottom-list">
                                        <li><Link to="#">Help</Link></li>
                                        <li><Link to="about">About</Link></li>
                                        <li><Link to="#">support</Link></li>
                                        <li><Link to="contact">contact</Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </footer>
                </main>
            </Switch>
            </Router>
        </>
    )
}
